<template>
	<view class="">
		<view class="charge">
				<view class="aa" v-for="(Fitem,index0) in datalist" :key='index0'>
					<uni-transition class="checkbox" :mode-class="['zoom-in']" :show="true">
						<view @click="firtst(Fitem.id)">
							<uni-icons type="arrowdown" v-if="Fitem.id==ids"></uni-icons>
							<uni-icons type="arrowright" v-else></uni-icons>
							<text style="font-size: 32rpx;">{{Fitem.name}}</text>
						</view>
					</uni-transition>
					<view class="aa" style="margin-left: 20rpx;" v-if="Fitem.id==ids?true:false" v-for="(Sitem,index) in Fitem.surgicalAssistDesignPriceVOS" :key='index'>
						<uni-transition class="checkbox" :mode-class="['zoom-in']" :show="true">
							<view  @click="second(Sitem.id,Sitem)">
								<uni-icons :type="Sicon" v-if="Sitem.surgicalAssistDesignPriceVOS.length!=0"></uni-icons>
								<text v-if="Sitem.name">{{Sitem.name}}</text>
								<text v-if="Sitem.price">：{{Sitem.price}}元</text>
							</view>
						</uni-transition>
						<view class="aa" style="margin-left: 40rpx;" v-if="Sitem.id==idss?true:false"  v-for="(Titem,index1) in Sitem.surgicalAssistDesignPriceVOS" :key='index1'>
							<view class="" @click="third(Titem.id,Titem)">
								<uni-icons v-if="Titem.surgicalAssistDesignPriceVOS.length!=0" type="arrowup"></uni-icons>
								<text v-if='Titem.name'>{{Titem.name}}</text>
								<text v-if="Titem.price">：{{Titem.price}}元</text>
							</view>
							<view class="aa"  style="margin-left: 60rpx;" v-if="Titem.id==idsss?true:false" v-for="(foitem,index2) in Titem.surgicalAssistDesignPriceVOS" :key='index2'>
								<view class="" @click="fourId(foitem.id,foitem)">
									<uni-icons v-if="foitem.surgicalAssistDesignPriceVOS.length!=0" type="arrowup"></uni-icons>
									<text v-if='foitem.name'>{{foitem.name}}</text>
									<text v-if="foitem.price">：{{foitem.price}}元</text>
								</view>
								<view class="aa" style="margin-left: 80rpx;" v-if="foitem.id==idssss?true:false" v-for="(fiveitem,index3) in foitem.surgicalAssistDesignPriceVOS" :key='index3'>
									<view class="">
										<uni-icons v-if="fiveitem.surgicalAssistDesignPriceVOS.length!=0" type="arrowup"></uni-icons>
										<text v-if='fiveitem.name'>{{fiveitem.name}}</text>
										<text v-if="fiveitem.price">：{{fiveitem.price}}元</text>
									</view>
								</view>
							</view>
						</view>
					</view>
				</view>
			
		</view>
		<u-no-network></u-no-network>
	</view>
</template>

<script>
	import {casePrice} from '@/utill/api/case/casePrice.js'
	export default{
		data(){
			return{
				ids:'',
				idss:'',
				idsss:'',
				idssss:'',
				datalist:[],
				Ficon:'arrowright',
				Sicon:'arrowright',
			}
		},
		methods:{
			firtst(id,index){
				this.ids=id
			},
			second(id,item){
				console.log('我是二级')
				console.log(item)
				this.idss=id
				this.Sicon='arrowdown'
			},
			third(id,item){
				console.log('我是三级')
				console.log(item)
				console.log(id)
				this.idsss=id
			},
			fourId(id,item){
				console.log('我是四级')
				console.log(item)
				console.log(id)
				this.idssss=id
			},
			queryPrice(){
				casePrice({}).then(res=>{
					console.log(res)
					this.datalist=res.data.object
				}).catch(err=>{
					console.log(err)
				})
			},
		},
		onLoad() {
			this.queryPrice()
		}
	}
</script>

<style scoped>
	.charge{
		padding: 0 30rpx;
	}
	.aa{
		padding: 10rpx 0;
		color: #000000;
		font-size: 28rpx!important;
		margin-top: 20rpx;
		background-color: #FFFFFF;
	}
	.aa .uni-icons{
		margin-right: 20rpx;
		font-size: 24rpx!important;
	}
	.slot-wrap{
		flex:1;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0 30rpx;
	}
</style>
